<template>
  <div :style="getStyle()" class="side">
    <div @click="$refs.fileInput.click()" class="pic">
      <img :src="imageURL" alt="perfil" class="img-pic"/>
    </div>
    <div v-if="exibirLinks" class="contato">
       <input type="file"
        id="input"
        ref="fileInput"  
        style="display:none;"
        @change="onIMGChange"
        />
        <p class="title">CONTATO<img src="../icons/editar.png" alt="editar" class="editar" @click="showEditarContato"/></p><br>
        <div v-for="(item, index) in email " :key="index" class="data-container">
            <img v-if="item" src="../icons/envelope.svg" class="email-icon"/>
            <span contenteditable="true" class="email-text">{{item}}</span>
        </div>
        <div v-for="(item, index) in phone" :key="index" class="data-container">
            <img v-if="item" src="../icons/phone.png" alt="phone" class="phone-icon">
            <span contenteditable="true" class="phone-text">{{item}}</span>
        </div>
        <div class="data-container">
            <img v-if="adress" src="../icons/adress.png" alt="adress" class="adress-icon">
            <span contenteditable="true" class="endereco-text">{{adress}}</span>
        </div>
    </div>
    <editar-contato
      class="editar-dados-contato"
      :phones="phone"
      :endereco="adress"
    />
    <Formacao
      v-if="exibirFormacao"
      class="template-data"
      titulo="FORMAO"
      :backgroundColor="cor"
      :formacao="grade"
    />
     <editarFormacao
      class="editar-dados-escolares"
      :grade="grade"
    />
    <Habilidade
      v-if="exibirHabilidade"
      class="template-data"
      titulo="HABILIDADES"
      backgroundColor="#808080"
      :hability="hability"
    />
    <editar-habilidade 
      class="editar-habilidade"
      :habilidade="hability"
    />
    <Social
      v-if="exibirSocial"
      class="template-data"
      titulo="SOCIAL LINKS"
      backgroundColor="#808080"
      :face="facebook"
      :lin="lin"
      :twitt="twitter"
      :you="youtube"
      :stof="stackoverflow"
    />
    <editar-social 
      class="editar-social"
      :face="facebook"
      :lin="lin"
      :twit="twitter"
      :you="youtube"
      :stof="stackoverflow"  
    />
</div>
</template>
<script>
import Formacao from "./Formacao.vue";
import Habilidade from "./Habilidade.vue";
import Social from "./Social.vue"
import editarContato from './editarContato.vue'
import editarFormacao from './editarFormacao.vue'
import editarHabilidade from './editarHabilidade.vue'
import editarSocial from './editarSocial.vue'

export default {
 components: {
    Formacao,
    Habilidade,
    Social,
    editarContato,
    editarFormacao,
    editarHabilidade,
    editarSocial
  },
  props:{
    cor: String,
  },
 name:'Side',
 data(){
   return{
    imageURL: "data:image/png;base64,",
    exibirLinks: true,
    exibirFormacao: true,
    exibirHabilidade: true,
    exibirSocial: true,
     email : ['insira seu email aqui @teste.com',
     ],
      phone : [
        'telefone'
      ],
   adress: 'seu Endereo',
   grade : ['Seus dados escolares'],
   hability : "Suas habilidades",
   facebook : null,
   lin : null,
   twitter : null,
   youtube : null,
   stackoverflow : null,
   }
 },
 methods: {
   getContatoData(){
     if(localStorage.getItem('useremail0')){
       this.email[0] = localStorage.getItem('useremail0')
     }
     if(localStorage.getItem('useremail1')){
       this.email[1] = localStorage.getItem('useremail1')
     }
     if(localStorage.getItem('useremail2')){
       this.email[2] = localStorage.getItem('useremail2')
     }

     // phones

     let arrayP = localStorage.getItem('phones')
     if(arrayP){
       this.phone = []
        arrayP.split(',').forEach(element => {
          this.phone.push(element)
        });
     }

     // adress

     let adressStorage = localStorage.getItem('adress')
     if(adressStorage){
       this.adress = adressStorage
     }

     // grade

     const gradeStorage = localStorage.getItem('grade')
     if(gradeStorage){
       this.grade = []
       this.grade = gradeStorage.split(',')
     }

     // habilitys

     const habilityStorage = localStorage.getItem('hability')
     if(habilityStorage){
       this.hability = habilityStorage
     }

     //social in links

     const lin = localStorage.getItem('in')
     if(lin){
       this.lin = lin
     }
     const you = localStorage.getItem('you')
     if(you){
       this.youtube = you
     }
     const stof = localStorage.getItem('stof')
     if(stof){
       this.stackoverflow = stof
     }
     const face = localStorage.getItem('face')
     if(face){
       this.facebook = face
     }
     const twit = localStorage.getItem('twit')
     if(twit){
       this.twitter = twit
     }
   },
    getStyle(){
      return{
        'background-color': `${this.cor}`
      }
    },
    onIMGChange(img){
      console.log(URL.createObjectURL(img.target.files[0]))
      this.imageURL = URL.createObjectURL(img.target.files[0])
      // the two codes works as well
      // document.getElementsByClassName("img-pic")[0].src = URL.createObjectURL(img.target.files[0])
    },
    showEditarContato(){
      document.getElementsByClassName('editar-contato-container')[0].style.display= 'block'
    }
  },
  beforeMount(){
    this.getContatoData()
  },
}
</script>
<style>
.editar{
  width: 15px;
  height: 15px;
  float: right;
}
.img-pic{
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
}
.side{
  display: block;
}
.template-data{
  width: 100%;
}
@media only screen and (max-width:1000px) {
  .side{
    height: calc(100% +40px);
    width: 100%;
    background-color: rgb(214, 86, 86);
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 10px;
  }
  .pic{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 2px solid black;
    align-self: center;
    margin: 0 auto;
  }
  .editar-dados-escolares{
    width: 100%;
    height: 100%;
    display: none;
    padding: 50px;
    background-color: whitesmoke;
  }
  .editar-habilidade{
    width: 100%;
    height: 100%;
    display: none;
    padding: 50px;
    background-color: whitesmoke;
  }
  .editar-social{
    width: 100%;
    height: 100%;
    display: none;
    padding: 50px;
    background-color: whitesmoke;
  }
}
@media screen and (min-width:1001px) {
  .side{
    height: calc(100% +200px);
    width: 200px;
    background-color: gray;
    justify-content: center;
    padding-top: 50px;
    font-size: 10px;
    overflow: hidden;
  }
  .pic{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid black;
    align-self: center;
    margin: 0 auto;
  }
  .editar-dados-escolares{
    width: 300px;
    max-height: 100%;
    border-radius: 20px;
    box-shadow: gray -2px 2px 2px;
    display: none;
    background-color: whitesmoke;
    margin-top: -200px;
    margin-left: 200px;
    opacity: 90%;
    padding: 10px;
    position: absolute;
  }
  .editar-habilidade{
    width: 300px;
    max-height: 100%;
    border-radius: 20px;
    box-shadow: gray -2px 2px 2px;
    display: none;
    background-color: whitesmoke;
    margin-top: -200px;
    margin-left: 200px;
    opacity: 90%;
    padding: 10px;
    position: absolute;
  }
  .editar-social{
    width: 400px;
    max-height: 100%;
    border-radius: 20px;
    box-shadow: gray -2px 2px 2px;
    display: none;
    background-color: whitesmoke;
    margin-top: -200px;
    margin-left: 200px;
    opacity: 90%;
    padding: 10px;
    position: absolute;
  }
}
@media print {
.editar-social{
  display: none;
}
.side{
    height: 100%;
    width: 37%;
    justify-content: center;
    padding-top: 50px;
    font-size: 10px;
    overflow: hidden;
  }
  .pic{
    width: 140px;
    height: 180px;
    border-radius: 50%;
    border: 2px solid black;
    align-self: center;
    margin: 0 auto;
  }

  .editar{
    display: none;
  }

  .editar-dados-escolares{
    display: none;
  }

  .editar-habilidade{
    display: none;
  }

  .editar-dados-contato{
    display:  none !important;
  }
  .editar-social{
    display: none !important;
  }
}

.title{
  align-self: center;
  background-color: white;
  color: black;
  font-weight: bolder;
  width: 80%;
  margin: 0 auto;
  margin-top: 50px;
  text-align: center;
  padding: 10px;
}
.data-container{
  display: flex;
  width: 85%;
  padding-bottom: 10px;
  align-self: center;
  margin: 0 auto;
  margin-top: 1px;
  margin-bottom: 10px;

}
.email-icon{
    width: 25px;
    height: 25px;
    margin-right: 10px;
}
.phone-icon{
    width: 25px;
    height: 25px;
    margin-right: 10px;
}
.adress-icon{
    width: 25px;
    height: 25px;
    margin-right: 10px;
}
</style>