<template>
    <div class="avatares-container">
        <p class="title">ESCOLHA UM AVATAR</p>
        <div class="avatar-line">
            <img class="avatar" id="a1"
                @click="changeAvatar"
                src="../icons/avatares/avatar1.png"
                alt="avatar"
            />
            <img class="avatar"
                @click="changeAvatar"
                id="a2"
                src="../icons/avatares/avatar2.png"
                alt="avatar"
            />
             <img class="avatar"
                @click="changeAvatar"
                id="a3"
                src="../icons/avatares/avatar3.png"
                alt="avatar"
            />
        </div>
        <div class="avatar-line">
            <img class="avatar"
                @click="changeAvatar"
                id="a4"
                src="../icons/avatares/avatar4.png"
                alt="avatar"
            />
            <img class="avatar"
                @click="changeAvatar"
                id="a5"
                src="../icons/avatares/no-img.png"
                alt="avatar"
            />
         </div>
        
    </div>
</template>
<script>
export default {
    name: "avatares",
    methods:{
        changeAvatar(id){
            console.log("o ide  ->"+id.target.id)
            if(id.target.id=="a1"){
                document.getElementsByClassName("pic")[0].style.display = "block"
                document.getElementsByClassName("img-pic")[0].src = "data:image/png;base64,"
            }else if(id.target.id=="a2"){
                document.getElementsByClassName("pic")[0].style.display = "block"
                document.getElementsByClassName("img-pic")[0].src = "http://localhost:8080/img/avatar2.9281ce7f.png"
            }
            else if(id.target.id=="a3"){
                document.getElementsByClassName("pic")[0].style.display = "block"
                document.getElementsByClassName("img-pic")[0].src = "data:image/png;base64,"
            }
            else if(id.target.id=="a4"){
                document.getElementsByClassName("pic")[0].style.display = "block"
                document.getElementsByClassName("img-pic")[0].src = "data:image/png;base64,"
            }
            else if(id.target.id=="a5"){
                document.getElementsByClassName("pic")[0].style.display = "none"
            }
        }
    }
}
</script>
<style scoped>
.avatar-line{
    display: flex;
    margin: 10px;
}
.avatar{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    margin-left: 10px;
}
.avatares-container{
    width: 100%;
    display: block;
    min-height: 80px;
}
</style>